.form {
    border: 10px solid #727a77;
    border-radius: 10px;
    padding: 30px 60px;
    background-color: #727a77;
    color: #f9f9f9;
    font-size: 1.25rem;
    text-align: center;
    max-width: 600px;
    width: 100%;
    box-sizing: border-box;
    margin: 0 auto;
}

@media (max-width: 768px) {
    .form {
        padding: 30px;
        font-size: 1rem;
    }
}

.form__labelText {
    margin: auto;
}

.form__field {
    display: flex;
    width: 100%;
    max-width: 300px;
    margin: 10px auto;
    padding: 10px;
    border-radius: 10px;
    border: 1px solid #f9f9f9;
    font-size: 1.25rem;
    box-sizing: border-box;
    text-align: center;
}

.form__field:hover {
    background-color: #cec6e3;
    transition: background-color 0.5s ease;
}

@media (max-width: 768px) {
    .form__field {
        max-width: 100%;
        padding: 10px;
        font-size: 1rem;
    }
}

.form__legend {
    color: #f9f9f9;
    font-size: 2rem;
}

@media (max-width: 768px) {
    .form__legend {
        font-size: 1.5rem;
    }
}

.form__fieldset {
    border-radius: 10px;
    padding: 40px;
    margin-bottom: 20px;
}

@media (max-width: 768px) {
    .form__fieldset {
        margin: 0 auto;
        padding: 20px;
    }
}